import { makeId } from '../id';
import { mapObject } from './util';
import Item from './Item';

export function makeDictClass(events) {
    return class MyDict {
        id;
        items;

        constructor(...params) {
            this.items = {};

            if ( params.length >= 1 ) {
                this.items = params[0];
            }

            let options;
            if ( params.length >= 2 ) {
                options = params[1];
            }

            this.id = makeId();
            this.name = options?.name;

            const itemsWithIds = mapObject(
                this.items,
                value => ({
                    value,
                    id: makeId()
                })
            );

            console.info("Created dict,", this.id);
            events.push({
                event: 'create',
                type: 'dict',
                componentId: this.id,
                name: options?.name,
                items: itemsWithIds,
                color: options?.color
            });

            return new Proxy(this, {
                get(target, prop) {
                    if ( String(prop) === prop && !(prop in target) ) {
                        return target.items[prop]
                    }
                    return target[prop];
                },
                set(target, prop, value) {
                    if ( String(prop) === prop ) {
                        target.items[prop] = value;
                        events.push({
                            event: 'setprop',
                            componentId: target.id,
                            name: this.name,
                            prop: prop,
                            item: {
                                value,
                                id: makeId()
                            }
                        });
                    }
                    target[prop] = value;
                    return true;
                }
            });
        }


    };
}

export default function DictComponent({ name, items, color }) {
    const sortedItems = [];
    const keys = Object.keys(items);
    keys.sort();

    for ( let i = 0; i < keys.length; i++ ) {
        const key = keys[i];
        sortedItems[i] = { key, item: items[key] };
    }

    return (
        <div className="dict" style={{ backgroundColor: color }}>
        <h2>{ name }</h2>
        <table>
        <tbody>
        { sortedItems.map(({ key, item }) => (
            <tr key={key}>
                <td className="dict-key">
                    {key}
                </td>
                <td className="dict-value">
                    <Item key={item.id} value={item.value} id={item.id}/>
                </td>
            </tr>
        )) }
                </tbody>
            </table>
        </div>
    );
};
