import { useEffect, useState } from 'react';

let prev = null;
function wrap(part) {

    switch ( part ) {
        case ':':
            prev = ':';
            return (<span className="json-infra">{part}</span>);
        case '{':
        case '}':
        case '[':
        case ']':
        case '"':
        case ',':
            prev = part;
            return (<span className="json-infra">{part}</span>);
        default:
            break;
    }


    if ( prev === ':' ) {
        if ( Number(part) === part ) {
            return (<span className="json-number">{part}</span>);
        }
    }
    if ( prev === '"' || prev === "'" ) {
        return (<span className="json-string">{part}</span>);
    }


    return (<span className="json-key">{part}</span>);
}

function tokenizeHelper(parts, x) {
    if ( x === null || x === undefined || isNaN(x) ) {
        parts.push(x);

    } else if ( typeof x === "string" ) {
        parts.push('"');
        parts.push(x);
        parts.push('"');

    } else if ( typeof x === "number" ) {
        parts.push(x);

    } else if ( typeof x === "object" && x.length !== undefined ) {
        parts.push("[");
        for ( const child of x ) {
            tokenizeHelper(parts, child);
            if ( child !== x[x.length - 1] ) {
                parts.push(",");
            }
        }
        parts.push("]");

    } else if ( typeof x === "object" ) {
        parts.push("{");
        const keys = Object.keys(x);
        for ( let key of keys ) {
            //tokenizeHelper(parts, key);
            parts.push(key);
            parts.push(":");
            const child = x[key];
            tokenizeHelper(parts, child);
            if ( key !== keys[keys.length - 1] ) {
                parts.push(",");
            }

        }
        parts.push("}");
    }
}

function tokenize(x) {
    const parts = [];

    tokenizeHelper(parts, x);

    return parts;
}

export default function Item({ value, id }) {
    const [effectStarted, setEffectStarted] = useState(true);
    useEffect(() => {
        console.log("effect");
        window.setTimeout(() => {
            setEffectStarted(false);
        }, 200);
    }, [id]);


    if ( typeof value !== 'object' ) {
        return (
            <span className={`item ${effectStarted ? 'changed' : ''}`}>
                {
                    value
                }
            </span>
        );
    }

    if ( typeof value.toString === "function" && value.toString() !== '[object Object]' ) {
        return (
            <span className={`item ${effectStarted ? 'changed' : ''}`}>
                {
                    value.toString()
                }
            </span>
        );
    }

    const parts = tokenize(value);
    console.log("Parts:", parts);

    return (
        <span className={`item ${effectStarted ? 'changed' : ''}`}>
        {
            parts.map(part => wrap(part))
        }
        </span>
    );
};
