import { makeId } from '../id';
import { mapObject } from './util';

import Item from './Item';

export function makeGlobalsClass(events) {
    return class MyGlobals {
        id;
        items;

        constructor(...params) {
            if ( params.length >= 1 ) {
                this.items = params[0];
            }

            let options;
            if ( params.length >= 2 ) {
                options = params[1];
            }

            this.id = makeId();
            this.name = options?.name;

            const itemsWithIds = mapObject(
                this.items,
                value => ({
                    value,
                    id: makeId()
                })
            );

            events.push({
                event: 'create',
                type: 'globals',
                componentId: this.id,
                name: options?.name,
                items: itemsWithIds,
                color: options?.color
            });

            let self = this;

            return new Proxy(this, {
                get(target, prop) {
                    if ( String(prop) === prop && !(prop in target) ) {
                        return self.items[prop];
                    }
                    return target[prop];
                },
                set(target, prop, value) {
                    if ( String(prop) === prop ) {
                        self.items[prop] = value;
                        events.push({
                            event: 'setprop',
                            componentId: self.id,
                            name: this.name,
                            prop: prop,
                            item: { value, id: makeId() },
                            id: prop
                        });
                    }
                    target[prop] = value;
                    return true;
                }
            });
        }


    };
}

export default function GlobalsComponent({ items, color }) {
    const keys = Object.keys(items);
    keys.sort();

    const sortedItems = [];
    for ( let i = 0; i < keys.length; i++ ) {
        const key = keys[i];
        sortedItems[i] = { key, item: items[key] };
    }

    return (
        <div className="globals" style={{ backgroundColor: color }}>
            { sortedItems.map(({ key, item }) => (
                <span key={key}>
                    <span className="globals-name">
                        {key} =
                    </span>
                    {' '}
                    <Item value={item.value} id={item.id} />
                </span>
            )) }
        </div>
    );
};
