import { makeId } from '../id';
import { isNumericString, isNumber, readParams } from './util';
import Item from './Item';


export function makeVectorClass(events) {
    return class MyVector {
        id;
        items;

        constructor(p1, p2, p3) { // ...params doesn't work here for some reason
            const params = [p1, p2, p3];
            const { size, options, items } = readParams(params, { size: 0, options: {}, items: [] });
            this.items = items ?? [];

            this.id = makeId();
            this.name = options?.name;

            console.info("Created array,", this.id);
            events.push({
                event: 'create',
                type: 'vector',
                componentId: this.id,
                name: options?.name,
                size,
                items: items.map(item => ({
                    value: item,
                    id: makeId()
                })),
                color: options?.color,
            });

            return new Proxy(this, {
                get(target, prop) {
                    if ( isNumber(prop) ) {
                        return target.items[prop]
                    }
                    return target.items[prop];
                },
                set(target, prop, value) {
                    if ( isNumber(prop) || isNumericString(prop) ) {
                        target.items[Number(prop)] = value;
                        events.push({
                            event: 'setitem',
                            componentId: target.id,
                            name: this.name,
                            index: Number(prop),
                            item: { value, id: makeId() },
                        });

                        return true;
                    }

                    return false;
                }
            });
        }
    };
}

export default function Vector({ name, items, color }) {
    const filledItems = [];
    items = items ?? [];
    for ( let i = 0; i < items.length; i++ ) {
        filledItems[i] = items[i];
    }
    return (
        <div className="vector" style={{ backgroundColor: color }}>
            <h2>{ name }</h2>
            <table>
                <tbody>
                    {
                        filledItems.map((item, index) => (
                            <tr key={index}>
                                <td className="vector-index">
                                    {index}
                                </td>
                                <td className="vector-value">
                                    <Item key={item.id} value={item.value} id={item.id} />
                                </td>
                            </tr>
                        ))
                    }
                </tbody>
            </table>
        </div>
    );
};
