import { makeId } from '../id';
import { readParams } from './util';
import Item from './Item';

export function makeStackClass(events) {
    return class Stack {
        id;
        items;
        constructor(p1, p2, p3) {
            const params = [p1, p2, p3];
            const { options, items } = readParams(params, { options: {}, items: [] });

            this.id = makeId();
            this.name = options?.name;

            console.info("Created stack,", this.id);
            events.push({
                event: 'create',
                type: 'stack',
                componentId: this.id,
                name: options?.name,
                items: items.map(item => ({
                    value: item,
                    id: makeId()
                })),
                color: options?.color,
            });
            this.items = [ ...items ];
        }

        push(value) {
            this.items.push(value);
            events.push({
                event: 'push',
                componentId: this.id,
                name: this.name,
                item: { value, id: makeId() },
            });
        }

        pop() {
            const value = this.items.pop();
            events.push({
                event: 'pop',
                componentId: this.id,
                name: this.name,
                item: { value, id: makeId() },
            });
            return value;
        }

        peek() {
            return this.items[this.items.length - 1];
        }

        get length() {
            return this.items.length;
        }
    };
}

export default function Stack({ name, items, color }) {
    return (
        <div className="stack" style={{ backgroundColor: color }}>
            <h2>{ name }</h2>
            <div>
                {
                    items.map((item, index) => (
                        <Item key={item.id} value={item.value} id={item.id} />
                    ))
                }
            </div>
        </div>
    );
};
