import { makeId } from '../id';
import { readParams } from './util';
import Item from './Item';
import LimboItem from './LimboItem';


export function makeQueueClass(events) {
    return class Queue {
        id;
        items;
        constructor(p1, p2, p3) {
            const params = [p1, p2, p3];
            const { options, items } = readParams(params, { options: {}, items: [] });

            this.id = makeId();
            this.name = options?.name;

            console.info("Created queue,", this.id);
            events.push({
                event: 'create',
                type: 'queue',
                componentId: this.id,
                name: options?.name,
                items: items.map(item => ({
                    value: item,
                    id: makeId()
                })),
                color: options?.color,

            });
            this.items = [ ...items ];
        }

        enqueue(value) {
            this.items.push(value);
            events.push({
                event: 'enqueue',
                componentId: this.id,
                name: this.name,
                item: { value, id: makeId() },
            });
        }

        dequeue() {
            const value = this.items.shift();
            events.push({
                event: 'dequeue',
                componentId: this.id,
                name: this.name,
                item: { value, id: makeId() },
            });
            return value;
        }

        front() {
            return this.items[0];
        }

        back() {
            return this.items[this.items.length - 1];
        }

        get length() {
            return this.items.length;
        }

    };
}

export default function Queue({ name, items, limbo, color }) {
    return (
        <div className="queue" style={{ backgroundColor: color }}>
            <h2>{ name }</h2>
            <div>
                { limbo &&
                  <LimboItem key={limbo.id} value={limbo.value} id={limbo.id} />
                }
                {
                    items.map((item, index) => (
                        <Item key={item.id} value={item.value} id={item.id} />
                    ))
                }
            </div>
        </div>
    );
};
