import { makeId } from '../id';
import { readParams } from './util';
import Item from './Item';

function minSort(a, b) {
    return a - b;
}

function maxSort(a, b) {
    return b - a;
}

export function makeHeapClass(events) {
    return class Heap {
        id;
        items;
        constructor(p1, p2, p3) {
            const params = [p1, p2, p3];
            const { options, items, func: comparator, string: compStr } =
                readParams(params, { options: {}, items: [], func: null, string: null });

            this.id = makeId();
            this.name = options?.name;

            if ( compStr && comparator ) {
                throw new Error("Can't give both comparator function or 'min'/'max'");
            }

            if ( compStr === "min" ) {
                this.comparator = minSort;
            } else if ( compStr === "max" ) {
                this.comparator = maxSort;
            } else {
                this.comparator = comparator ?? minSort;
            }

            items.sort(this.comparator);
            this.items = items;

            console.info("Created heap,", this.id);
            events.push({
                event: 'create',
                type: 'heap',
                componentId: this.id,
                name: options?.name,
                items: items.map(item => ({
                    value: item,
                    id: makeId()
                })),
                color: options?.color,
            });
        }

        insert(value) {
            this.items.push(value);
            this.items.sort(this.comparator);
            events.push({
                event: 'insert',
                componentId: this.id,
                name: this.name,
                item: { value, id: makeId() },
                comparator: this.comparator
            });
        }

        extract() {
            console.log("this.itmees:", this.items);
            const value = this.items.shift();
            events.push({
                event: 'extract',
                componentId: this.id,
                name: this.name,
                item: { value, id: makeId() },
            });
            return value;
        }

        front() {
            return this.items[0];
        }

        back() {
            return this.items[this.items.length - 1];
        }

        get length() {
            return this.items.length;
        }

    };
}

export default function Heap({ name, items, color }) {
    return (
        <div className="heap" style={{ backgroundColor: color }}>
            <h2>{ name }</h2>
            <div>
                {
                    items.map((item, index) => (
                        <Item key={item.id} value={item.value} id={item.id} />
                    ))
                }
            </div>
        </div>
    );
};
